<section class="portfolio" id="portfolioInner"><div class="container"><div class="portfolio-wrap"><h3 class="portfolio__title neon" v-html="portfolio.neonPortfolioTitle"></h3><nav class="portfolio__nav"><label v-for="item in portfolioInner.menu" :key="item.id" :class="item.labelClass" v-if="portfolioNavHidden"><input type="radio" name="checkbox" :id="item.id" :class="item.class" checked :data-menu="item.dataMenu" @click="getCheck($event), scrollToTop()"><span class="portfolio__nav-label-text">{{ item.text }}</span></label><my-dropdown :dropPort="dropdownPortfolio" :menu="portfolioInner.menu" :dropHidden="dropHidden" :portfolioNavHidden="portfolioNavHidden" @dropdown="dropdown" @dropdownOption="dropdownOption"></my-dropdown></nav><div class="portfolio__card-inner" :data-portfolio-filter="portfolioInnerFilter"><div class="project" v-for="item in portfolioAllFilter" :key="item" :class="item.allMenuClass"><my-card :link="'#'" :cardInfo="item"></my-card></div></div></div></div><div class="portfolio__background"></div></section>