<section class="card-post"><div class="container card-post-wrap"><div class="card-post__nav"><h2 class="card-post__nav-desc">{{ activeCard.card.cardPostTitle }}</h2><h3 class="card-post__nav-title">{{ activeCard.card.cardPostName }}</h3><div class="card-post__nav-meta"> <span v-for="item in activeCard.card.cardPostMeta" :key="item" :class="item.class">{{ item.text }}</span></div></div><div class="card-post__content"><div class="card-post__content-desc"><card-link :linkProjectTitle="activeCard.card.linkProjectTitle" :linkProjectItem="activeCard.card.linkProjectItem"></card-link><card-about :cardAboutTitle="activeCard.card.cardAboutTitle" :cardAboutText="activeCard.card.cardAboutText"></card-about><card-detals :detalsTitle="activeCard.card.detalsTitle" :detalsDescItems="activeCard.card.detalsDescItems"></card-detals></div><div class="card-post__content-carousel"><splide class="carousel-project" :options="projectSlide" :key="'slideCardKey' + slideCardKey"><splide-slide v-for="item in activeCard.card.projectSlides" :key="item" @click="fullPageAdd($event)" @mousedown="mousedownSlide" @mouseup="mouseupSlide"><img :src="require('@/assets/img/' + item.src)" :alt="item.alt"></splide-slide></splide></div></div><div class="card-post__pagination"><my-pagination :paginationPrev="pagination.prev.ttl" :paginationNext="pagination.next.ttl" @clickActionPrev="prevProject()" @clickActionNext="nextProject()"></my-pagination></div></div></section>