<div class="portfolio__dropdown checked" :class="dropPort.labelClass" v-if="!portfolioNavHidden"><div class="portfolio__dropdown-active" @click="$emit('dropdown', $event)">{{ dropPort.text }}</div><div class="portfolio__dropdown-list" v-if="!dropHidden"><div class="portfolio__dropdown-item" @click="$emit('dropdownOption', $event)" v-for="item in menu" :key="item.id" :class="item.labelClass" :data-menu="item.dataMenu">{{ item.text }}</div></div></div>